body {
    background: rgb(47, 133, 212);
    background: -webkit-gradient(linear, left top, right top, from(rgb(73, 144, 182)), color-stop(rgb(47, 133, 212)), to(rgb(56, 86, 219)));
    background: -webkit-linear-gradient(left, rgb(73, 144, 182), rgb(47, 133, 212), rgb(56, 86, 219));
    background: -o-linear-gradient(left, rgb(73, 144, 182), rgb(47, 133, 212), rgb(56, 86, 219));
    background: linear-gradient(to right, rgb(73, 144, 182), rgb(47, 133, 212), rgb(56, 86, 219));
    height: 100vh;
    margin: 0;
    font-family: Futura, "Trebuchet MS", Arial, sans-serif;
}

#app {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column wrap;
        flex-flow: column wrap;
}

#header {
    text-align: center;
    font-size: 3em;
    padding: 20px;
}

#page-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: center;
        justify-content: center;
    min-height: 85vh;
}

#note-menu, #active-note {
    -webkit-box-shadow: 0px 0px 50px 10px rgba(47, 133, 212, 1);
    box-shadow: 0px 0px 50px 10px rgba(47, 133, 212, 1);
    border-radius: 10px;
}

#note-menu {
    background-color: white;
    min-width: 30%;
    max-width: 30%;
    z-index: 2;
    position: relative;
}

#note-header {
    font-size: 2em;
    width: 90%;
    margin: auto;
    padding: 10px 5px 0 5px;;
    border: none;
    border-bottom: 2px solid rgb(47, 133, 212);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

input, input:focus {
    border: none;
    outline: none;
    width: 100%;
}

#notes-list {
    width: 100%;
    overflow: hidden;
    padding-bottom: 50px;
}

.note {
    width: 90%;
    position: relative;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.note-text {
    width: 90%;
    margin-right: 10%;
}

#delete {
    position: absolute;
    right: 5px;
    top: 10px;
}

#clear {
    position: absolute;
    bottom: 5px;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
}

#active-note {
    background-color: white;
    min-width: 60%;
    max-width: 60%;
    font-size: 3em;
    padding: 2% 2% 0 2%;
}

@media screen and (min-width: 956px) {
    #active-note {
        margin-left: -5px;
    }
}

@media screen and (max-width: 955px) {
    #note-menu, #active-note {
        min-width: 100%;
    }
}


